@import "../../common/sass/sass";

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: $medium){
    width: 100%;
  }

  .block {
    width: 60%;
    height: 60%;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.8);
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    transition-property:height;
    transition-timing-function: ease-in-out;
    transition-duration: 1s;
    overflow: auto;
    overflow-x: hidden;
    @include scrollbar();
    @media screen and (max-width: $medium){
      width: 100%;
      height: 90%;
      border-radius: 0;
    }

    .searchBlock {
      margin-top: 40px;
      display: flex;
      justify-content: center;
      width: 100%;
      @media screen and (max-width: $medium){
        margin-top: 10px;
      }
    }

    .dataBlock {
      width: 70%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      .cityName {
        font-weight: 600;
        font-size: 40px;
        font-family: 'Poppins', sans-serif;
        color: white;

        .country {
          font-weight: 400;
          font-size: 16px;
          font-family: 'Poppins', sans-serif;
          color: rgba(255, 255, 255, 0.5);
          text-align: left;
          display: flex;
          gap: 10px;

          span {
            color: white;
            font-size: 20px;
          }
        }
      }

      .weather {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        font-family: 'Poppins', sans-serif;
        color: white;
        font-weight: 500;
        font-size: 20px;
        gap: 8px;

        span {
          color: rgba(255, 255, 255, 0.5);
          font-size: 16px;
          padding: 0 10px;
        }

        div {
          text-align: left;
        }
        .windDirecrion{
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: flex-start;
          gap:20px;
          .compassBlock{
            width: 80px;
            height: 80px;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 2;
            background: url('../../assets/image/Compass_360_(en).png') no-repeat;
            background-size: 100%;
            filter: invert(1);
          }
        }

        .temperature {
          font-weight: 800;
          font-size: 50px;
        }

        .clouds {
          display: flex;
          flex-direction: column;

          .iconBlock {
            display: flex;
            flex-direction: row;
            align-items: center;
          }
        }
      }
    }
  }
  .blockLong{
    @extend .block;
    height: 100%;
    justify-content: space-around;
    gap:40px;
  }
}